body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 20px;
  display: flex;
  justify-content: center;
}

.container {
  text-align: center;
  width: 80%;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
}

.employee-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.form-left,
.form-right {
  display: flex;
  flex-direction: column;
  width: 48%;
}

.form-left input,
.form-right input {
  margin: 8px 0;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
}

.add-btn {
  display: block;
  width: 200px;
  padding: 12px;
  font-size: 16px;
  color: white;
  background: #007bff;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  margin: 20px auto;
}

.add-btn:hover {
  background: #0056b3;
}

.employee-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.employee-table th,
.employee-table td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: center;
}

.employee-table th {
  background-color: #007bff;
  color: white;
}

.delete-btn {
  padding: 6px 12px;
  font-size: 14px;
  color: white;
  background: red;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.delete-btn:hover {
  background: darkred;
}
